import convertDuration from '../jsModules/convert-duration';

const availableTimes = {
  methods: {

    checkIfTimeIsBlockedForHoliday(time) {
      let blocked = false;
      let endDate = false;
      const employeeHolidays = this.$store.getters.employeeHolidays;
      
      employeeHolidays.forEach(elm => {
        if (time > elm.startDate && time < elm.endDate) {
          blocked = true;
          endDate = elm.endDate;
        }
      });
      return {
        endDate,
        blocked
      };
    },

    async getAvailableTimes() {
      const getters = this.$store.getters;
      const employee = getters.employees[getters.selectedEmployee.index];
      const serviceID = getters.selectedService.index;
      const selectedDate = getters.selectedDate;
      let selectedDateIndex = selectedDate.weekindex;
      const service = getters.services[serviceID];
      let dayOfEmployee = employee.days[selectedDateIndex];
      const blockedTimes = await this.getBlockedTimes();

      let availableTimes = null;
      
      if (employee.worksAsOpen) {
        dayOfEmployee = getters.shopDays[selectedDateIndex];
      }
      
      // If employee is working
      if (dayOfEmployee.times && dayOfEmployee.times.length > 0) {
        
        availableTimes = [];
        dayOfEmployee.times.forEach((time) => {
          const startTime = time[0].split(':');
          const endTime = time[1].split(':');
          const dateStart = new Date(selectedDate.date.getTime());
          const dateEnd = new Date(selectedDate.date.getTime());

          dateStart.setHours(parseInt(startTime[0]), parseInt(startTime[1]), 0 , 0);
          dateEnd.setHours(parseInt(endTime[0]), parseInt(endTime[1]), 0, 0);
          
          // convert duration of service in milliseconds
          const serviceTimeInMillisec = convertDuration({
            duration: service.duration
          });

          let dateStartTime = dateStart.getTime();
          let dateEndTime = dateEnd.getTime();
          
          // While starttime is lower or equal to endtime
          while (dateStartTime <= dateEndTime) {
            let isPossible = true;
            let blockedEventEnd;

            let blockedForHolidays = false;
            let isTimeIsBlockedForHoliday = null;

            if ((dateStartTime + serviceTimeInMillisec) <= dateEndTime) {
              
              for (let key in blockedTimes) {
                const event = blockedTimes[key];
                const eventStart = parseInt(event.timestamp);
                
                const eventEnd = eventStart + convertDuration({
                  duration: event.duration
                });

                // Is dateStarttime an already taken event OR
                // Is event + duration bigger than taken event AND starttime is smaller than eventEnd
                if (dateStartTime === eventStart || 
                    ((dateStartTime + serviceTimeInMillisec) > eventStart) && dateStartTime < eventEnd) {
                  
                  // Save Endtime of blockedEvent only once
                  if (isPossible) { blockedEventEnd = eventEnd; }
                  isPossible = false;
                }
              }

              isTimeIsBlockedForHoliday = this.checkIfTimeIsBlockedForHoliday(dateStartTime + serviceTimeInMillisec);
              if (isTimeIsBlockedForHoliday.blocked) {
                isPossible = false;  
              }
              
              if (isPossible) {
                const now = new Date().getTime();
                if (dateStartTime > now) {
                  availableTimes.push(new Date(dateStartTime));
                }
              }
            }

            

            // If time is not possible, push end time of event
            if (isPossible) {
              dateStartTime = dateStartTime + serviceTimeInMillisec;
            } else {
              if (isTimeIsBlockedForHoliday.blocked) {
                dateStartTime = isTimeIsBlockedForHoliday.endDate;
              } else {
                dateStartTime = blockedEventEnd;  
              }
            }
          }

        });
      }

      return new Promise((resolve) => {
        resolve(availableTimes)
      });
    
    },

    getBlockedTimes() {
      const _this = this;
      const getters = _this.$store.getters;
     // const timestamp = getters.selectedDate.date.getTime();
      const employeeName = getters.selectedEmployee.name;
      const events = this.$firebase.database().ref('events');

      return new Promise((resolve) => {
        events.orderByChild('employee').equalTo(employeeName).once('value', (snapshot) => {
          if (snapshot.val()) {
            resolve(snapshot.val());
          } else {
            resolve(false);
          }
        });
      });
    },
  }
}

export default availableTimes;